.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  min-height: 100vh;
  margin: 0;
  box-sizing: border-box;
}


.App-header {
  background-color: #f69336;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 5px 0;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.App-header h1 {
  font-size: calc(5vmin);
  line-height: 0;
  font-weight: bold;
}


.App-logo {
  height: calc(15vmin);
  margin-right: 20px;
}

.form-container {
  background-color: #ffffff;
  padding: 15px;
  font-size: 16px;
  border: 3px solid #f69336;
  border-radius: 30px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 70%;
  max-width: 350px;
  margin-top: 15px;
}

.form-container>* {
  margin-bottom: 15px;
}

.form-container>*:last-child {
  margin-bottom: 0;
}

.location-input-group {
  display: flex;
  align-items: center;
  width: 100%;
}

.location-label {
  font-size: 16px;
  color: #000000;
  margin-bottom: 5px;
}

.location-input-group input {
  margin-right: 10px;
  flex: 1;
}

.or-divider {
  font-size: 16px;
  font-weight: bold;
  color: #000000;
  margin-bottom: 10px;
}

button {
  background-color: #f69336;
  color: rgb(0, 0, 0);
  border: 2px solid #0e0e0e;
  padding: 10px 15px;
  font-size: 16px;
  font-weight: 600;
  border-radius: 18px;
  cursor: pointer;
}

button:hover {
  background-color: #f9ad66;
}

.loading-message {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 24px;
  font-weight: bold;
  color: white;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 20px 30px;
  border-radius: 8px;
  z-index: 1000;
}

.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
}

#map {
  width: 100%;
  height: 70vh;
  margin-top: 10px;
  border-radius: 10px;
  min-height: 400px;
  max-height: 600px;
}

.pac-container {
  width: 90% !important;
  max-width: 300px;
  font-size: 16px;
}

.pac-item {
  padding: 10px !important;
}

.pac-item-query {
  font-size: 16px !important;
}

@media (max-width: 768px) {
  .form-container {
    width: 95%;
  }

  .location-input-group {
    flex-direction: column;
  }

  .location-input-group input {
    margin-right: 0;
    margin-bottom: 10px;
  }

  .App-header {
    flex-direction: row;
    padding: 10px;
  }

  .App-header h1 {
    font-size: calc(5vmin);
  }

  .App-logo {
    height: 10vmin;
  }

  #map {
    height: 40vh;
  }

}

@media (min-width: 769px) {
  .location-input-group {
    flex-direction: row;
  }

  .location-input-group input {
    margin-right: 10px;
    margin-bottom: 0;
  }
}